
import Component, { mixins } from 'vue-class-component';
import TableMixin from '../../mixins/table';
import ZoneEidt from '@/components/ZoneEdit.vue';
import { State } from 'vuex-class';
import { ZoneMode } from '@/store';
import ZoneMixin from '@/mixins/zone';

@Component({
    components: {
        'zone-input': ZoneEidt
    }
})
export default class Fence extends mixins(TableMixin, ZoneMixin) {
    public activeNames: string[] = ['info', 'add'];
    public zone: IZone | null = null; // 

    // ===================================table
    public colCfg: any[] = [
        { prop: 'name', label: '', sortable: true, width: 90 },
        { prop: 'status', label: '', width: 60 }
    ];
    public operation: any[] = [
        {
            type: { default: 'primary' },
            name: 'display',
            desc: { default: '' }
        }
    ];
    // =====================================

    // =======================form
    public form: any = {
        name: '',
        mode: 2,
        open: true
    };
    // ====================================
    @State private readonly zoneMode!: ZoneMode;

    public created() {
        if (this.permission.delete) {
            this.operation.push({ type: 'danger', name: 'del', desc: '' });
        }
        if (this.permission.put) {
            this.operation.push({ type: 'warning', name: 'setting', desc: '' });
        }

        this.form.mode = this.zoneMode.in;
    }

    /**
     * 
     */
    public async del(row: IZone, index: number) {
        try {
            await this.$confirm(`${row.name}?`, '');
            await this.$http.post('/api/zone/deleteZone', { id: row.id });
        } catch (e) {
            return console.log(e);
        }

        this.refresh()
            .display(row, index, true)
            .$message.success('');
    }
    /**
     * 
     */
    public display(row: IZone, index: number, isDel?: boolean) {
        const i = this.operation.findIndex(v => v.name === 'display');
        if (i > -1) {
            const op = this.operation[i];
            if (op.type[index] || isDel) {
                op.type[index] = undefined;

                if (this.mgr) {
                    this.mgr.remove(row.name);
                }
            } else {
                op.type[index] = 'success';

                if (this.mgr) {
                    this.mgr.zoneOpen(row);
                }
            }
            op.desc[index] = op.desc[index] || isDel ? undefined : '';

            this.$set(this.operation, i, op);
        }

        return this;
    }

    // 
    public onSubmit() {
        if (!this.form.name) {
            return this.$message.error('');
        }

        const pointsCount = this.points.length;
        if (pointsCount < 3) {
            return this.$message.warning('3');
        }

        if (this.form.mode === this.zoneMode.switch && pointsCount !== 4) {
            return this.$message.warning('4');
        }

        this.put();
    }

    // 
    public async update() {
        if (this.zone) {
            if (!this.zone.name) {
                return this.$message.error('');
            }

            if (this.zone.mode === this.zoneMode.switch) {
                const pointsCount = this.zone.position.length;
                if (pointsCount < 4) {
                    return this.$message.warning('4, ');
                }

                if (pointsCount > 4) {
                    try {
                        await this.$confirm(`${pointsCount}, ${pointsCount - 4}`);
                        this.zone.position = <TPosition>this.zone.position.slice(0, 4);
                    } catch (e) {
                        return;
                    }
                }
            }

            const data: IZone = {
                ...this.zone,
                position: JSON.stringify(this.zone.position),
                updateTime: Date.now(),
                enable: this.zone.open ? 1 : 0
            };

            this.$http
                .post('/api/zone/updateZone', data, { 'Content-Type': 'application/json' })
                .then(() => this.refresh().$message.success(''))
                .catch(console.log);
        }
    }

    protected async fetch(page: number, pageSize: number) {
        let data: any[] = [];
        let count: number = 0;
        try {
            const res = await this.$http.get('/api/zone/getall', {
                pageSize,
                currentPage: page
            });
            data = res.pagedData.datas.map((v: IZone) => {
                v.status = v.enable ? '' : '';
                v.position = JSON.parse(<string>v.position);
                v.open = !!v.enable;
                v.mode = v.mode == null ? 0 : v.mode;

                return v;
            });

            count = res.pagedData.totalCount;
        } catch (e) {
            console.log(e);
        }

        return { count, data };
    }

    // 
    private put() {
        this.$confirm('', '', { type: 'info' })
            .then(this.submitAddZone.bind(this))
            .then(() =>
                this.remove()
                    .resetForm()
                    .refresh()
                    .$message.success('')
            )
            .catch(console.log);
    }

    // ()
    private submitAddZone(): Promise<ResponseData> {
        const now = Date.now();
        if (!this.mgr) {
            this.$message.error(', !');
            return Promise.reject('');
        }

        const data: IZone = Object.assign({}, this.form, {
            position: JSON.stringify(this.points.map(v => this.mgr!.getCoordinate(v))),
            enable: this.form.open ? 1 : 0,
            createTime: now,
            updateTime: now
        });

        return this.$http.post('/api/zone/addZone', data, { 'Content-Type': 'application/json' });
    }

    /**
     * 
     */
    private resetForm() {
        this.form = {
            name: '',
            mode: this.zoneMode.in,
            open: true
        };

        return this;
    }
}
